.projects-wrapper {
	.projects-grid__header {
		background: #f6f8fa;
	}

	&__view-more {
		margin: 0 auto;
		margin-top: 58px;
		border: 2px solid #093359;
		background: none;
		color: #093359;
	}
	@media screen and (max-width: $sm) {
		&__view-more {
			display: none;
		}
		.projects-grid__project {
			padding: 0;
		}
	}
}
