.post-inner {
	padding-top: 25px;
	padding-bottom: 20px;
	&__title {
		font-size: 24px;
		font-weight: 500;
	}
	&__content {
		margin-top: 50px;
	}
	@media screen and (max-width: $sm) {
		padding-top: 0px;
		&__title {
			font-size: 26px;
			font-weight: 400;
		}
		&__content {
			margin-top: 30px;
		}
	}
}
