.benefits {
	background-color: $base-gray;
	@include container-with-tabs;

	&__wrapper {
		display: flex;
		flex-direction: column;
	}
	&__item {
		padding: 30px 0;
		display: flex;
		gap: 90px;
		&:not(:last-child) {
			border-bottom: 1px solid rgba(110, 117, 123, 0.3);
		}
		&-name {
			font-weight: 500;
			width: calc(320 / 1180 * 100%);
		}

		&-descr {
			width: calc(770 / 1180 * 100%);
		}
	}
	@media screen and (max-width: $sm) {
		&__item {
			padding: 20px 0;
			flex-direction: column;
			gap: 20px;
			& > * {
				width: 100%;
			}
		}
	}
}
