.posts-swiper {
	display: flex;
	flex-direction: column;
	row-gap: 50px;
	position: initial;
	padding-bottom: 176px;
	&__footer {
		position: absolute;
		bottom: 0;
		right: 0;
		padding-left: 130px;
		span {
			font-weight: 500;
			color: #4d565d;
		}
		> * {
			margin-right: 34px;
			margin-top: 18px;
		}
	}
	&__tags {
		display: flex;
		justify-content: flex-start;
		align-content: flex-start;
		max-width: calc(100% - 290px);
		flex-wrap: wrap;
		column-gap: 40px;
		row-gap: 20px;
	}
	&__tag {
		display: inline-block;
		text-decoration: underline;
	}
	&__btns {
		float: right;
		margin-right: 0;
		padding: 40px 30px;
		padding-right: 120px;
		display: flex;
		gap: 20px;
		background: #fff;
	}
	&__btn {
		padding: 0;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 80px;
		height: 50px;
	}
	@media screen and (max-width: $lg) {
		&__footer {
			padding-left: 20px;
		}
	}
	@media screen and (max-width: $sm) {
		&__btns {
			padding: 27px 32px;
			padding-left: 18px;
			gap: 15px;
		}

		&__btn {
			width: 60px;
			height: 38px;
		}
	}
}
