.breadcrumbs {
	.container {
		padding: 35px 0;
	}
	&__list {
		display: flex;
		gap: 10px;
		list-style-type: none;
	}
	&__item {
		color: rgba(172, 175, 177, 1);
		a {
			color: rgba(172, 175, 177, 1);
		}
		&:not(:last-child) {
			&::after {
				content: "/";
				margin-left: 10px;
				color: #000;
			}
		}
	}
	@media screen and (max-width: $xl) {
		padding-left: 130px;
	}
	@media screen and (max-width: $lg) {
		padding-left: 20px;
	}
}
