.header {
	position: sticky;
	z-index: 10;
	top: 0;
	background-color: #fff;
	border-bottom: $base-color 2px solid;
	&__container {
		display: flex;
		justify-content: space-between;
		padding: 30px 0 20px 0;
	}

	&__inner {
		display: flex;
		flex-direction: column;
		gap: 30px;
	}
	&__contacts {
		display: flex;
		justify-content: right;
		align-items: center;
		gap: 20px;
	}
	&__icons {
		display: flex;
		align-items: center;
		gap: 20px;
	}
	&__icon {
		height: 25px;
	}
	&__btn {
		width: 200px;
		&--mobile {
			display: none;
			width: 30px;
			height: 30px;
		}
	}
	&__phone,
	&__phone a {
		font-weight: 500;
		font-size: 20px;
		line-height: 1.3;
		color: $base-color;
		text-decoration: none;
	}

	.menu {
		display: flex;
		gap: 70px;
		&__list {
			display: flex;
			justify-content: space-between;
			gap: 40px;
			list-style: none;
		}
		&__submenu {
			display: none;
			padding: 30px 40px 45px;
			position: absolute;
			top: 53px;
			box-shadow: 0 4px 4px 0 rgba(10, 67, 120, 0.25);
			border-bottom: $base-color 2px solid;
			background-color: $base-gray;
			max-height: 450px;
			z-index: 2;
			&-list {
				width: 100%;
				display: flex;
				list-style: none;
				flex-direction: column;
				&--about {
					text-wrap: nowrap;
					gap: 20px;
				}
			}
			&--active {
				display: flex;
			}
			&--uslugi {
				position: fixed;
				top: 160px;
				left: 50%;
				height: 450px;
				// width: calc(100% - paddings($container-padding-left, $container-padding-right));
				transform: translateX(-50%);
			}
		}
		&__item {
			position: relative;
			cursor: pointer;
			&--parent {
				margin-right: 20px;
				&::after {
					content: "";
					display: block;
					width: 12px;
					height: 8px;
					position: absolute;
					top: 55%;
					transform: translateY(-50%);
					right: -20px;
					background-image: url("../img/sprites/arrow-down.svg");
				}
			}
		}
		&__link {
			font-family: var(--font-family);
			font-size: 20px;
			line-height: 1.5;
			color: #4d565d;
		}
		.lang-switch {
			display: flex;
			align-items: center;
			gap: 25px;
			&__icon {
				height: 17px;
				width: 17px;
			}
			&__list {
				display: flex;
				align-items: center;
				gap: 6px;
			}
			&__link {
				color: #4d565d;
				font-weight: 500;
			}
			&__divider {
				width: 1px;
				height: 14px;
				background-color: black;
			}
		}
		&-mobile {
			width: 100%;
			left: 0;
			position: absolute;
			z-index: 10;
			display: none;
			top: calc(100% + 2px);
			height: calc(100vh - 100% - 2px);
			background: #fff;
			padding-bottom: 25px;
			.tabs {
				background: $base-gray;
				position: relative;
				padding: 25px 20px;
				padding-bottom: 0;
				height: calc(100% - 131px);
				display: flex;
				flex-direction: column;
				gap: 20px;
				&__content {
					height: calc(100% - 174px);
					margin-top: 10px;
					.swiper-slide {
						overflow-y: auto;
						overflow-x: hidden;
					}
				}
			}
			&--active {
				display: block;
			}
			&__container {
				height: 100%;
				display: flex;
				flex-direction: column;
				gap: 20px;
			}
			&__lists {
				position: relative;
				z-index: 5;
				background: #fff;
				display: flex;
				justify-content: space-around;
				padding: 10px 0;
				border-bottom: 1.5px solid #1f4569;
				border-top: 1.5px solid #1f4569;
			}
			&__list {
				width: 100%;
				list-style-type: none;
				display: flex;
				flex-wrap: wrap;
				flex-direction: column;
				justify-content: space-between;
				align-content: space-around;
				gap: 15px;
				text-align: left;
				padding: 5px 0;
				&:not(:last-child) {
					border-right: 0.5px solid rgba(110, 117, 123, 0.3);
				}
			}
			&__item,
			&__link {
				font-weight: 500;
				line-height: 1.3125;
				text-transform: uppercase;
				color: #6e757b;
			}
		}
	}
	@media screen and (max-width: $lg) {
		.container {
			padding: 20px;
		}
	}
	@media screen and (max-width: $md) {
		&__container {
			gap: 25px;
			justify-content: space-between;
		}
		&__contacts {
			flex-wrap: wrap;
			gap: 16px;
			height: 100%;
		}

		&__btns {
			display: flex;
			gap: 10px;
		}
		&__btn {
			&--mobile {
				width: 50px;
				height: 50px;
				padding: 7px 6px ;
				display: flex;
				svg {
					height: 100%;
				}
			}
		}
		.menu {
			display: none;
		}
	}
	@media screen and (max-width: $sm) {
		&__icons {
			gap: 12px;
		}
		&__icon {
			height: 18px;
			a {
				display: block;
				height: 100%;
				img {
					height: 100%;
				}
			}
		}

		&__phone,
		&__phone a {
			font-size: 15px;
		}
		&__btn {
			width: 122px;
			height: 30px;
			&--mobile {
				width: 30px;
				height: 30px;
			}
		}
	}
}

.categories-list {
	display: flex;
	flex-direction: column;
	flex-wrap: wrap;
	align-items: stretch;
	align-content: space-between;
	text-wrap: wrap;
	list-style: none;
	width: 100%;
	gap: 10px;
	&__item {
		width: 315px;
		font-family: var(--font-family);
		font-weight: 400;
		color: #0a4378;
		&--tree {
			position: relative;
			& > .categories-list__link::after {
				content: "+";
				position: absolute;
				right: -20px;
				font-weight: 500;
				font-size: 20px;
				color: #0a4378;
				line-height: 1;
				top: 50%;
				transform: translateY(-50%);
			}
			summary {
				list-style-type: none;
				.categories-list__link::after {
					content: "+";
					position: absolute;
					right: -20px;
					font-weight: 500;
					font-size: 20px;
					color: #0a4378;
					line-height: 1;
					top: 50%;
					transform: translateY(-50%);
				}
			}

			details[open] {
				summary {
					.categories-list__link::after {
						content: "-";
					}
				}
			}
		}
	}
	&__link {
		position: relative;
		color: #000;
		text-wrap: wrap;
		cursor: pointer;
		&:hover {
			color: #0a4378;
		}
	}
	&__sublist {
		list-style: none;
		display: flex;
		flex-direction: column;
		margin-top: 10px;
		gap: 10px;
		padding-left: 20px;
		border-left: 1px solid rgba(110, 117, 123, 0.3);
		.categories-list__item {
			width: 295px;
		}
		&--collapsed {
		}
	}
	&.categories-list--practice {
		gap: 30px;
		.categories-list__item--tree > a {
			font-weight: 500;
			&::after {
				content: "";
			}
		}
	}
	&--widget {
		gap: 10px;
	}
}
