.sidebar {
	display: flex;
	flex-direction: column;
	gap: 50px;
	@media screen and (max-width: $sm) {
		gap: 20px;
	}
}

.widget {
	padding: 47px 27px;
	background: #f6f8fa;
	&__title {
		/* Tablist */
		height: 60px;
		mix-blend-mode: normal;
		border-width: 2px 0px;
		border-style: solid;
		border-color: #0a4378;
		margin-bottom: 40px;
		font-weight: 500;
		text-align: center;
		color: #6e757b;
		padding: 20px 0;
		line-height: 1.3;
	}

	ul {
		list-style: none;
	}
	&__inner {
		display: flex;
		flex-direction: column;
		gap: 20px;
	}
	.authors {
		gap: 35px;

		&__item {
			display: flex;
			justify-content: space-between;
			gap: 24px;
		}
		&__img {
			width: 100%;
			height: 100%;
			&-wrapper {
				width: 82px;
				height: 102px;
			}
		}

		&__content {
			display: flex;
			flex-direction: column;
			font-family: var(--second-family);
		}
		&__name {
			font-weight: 400;
			font-size: 18px;
			line-height: 1.27;
			color: #14121d;
		}
		&__position {
			font-weight: 400;
			font-size: 12px;
			line-height: 1.5;
			color: #6e757b;
		}
		&__btn {
			display: flex;
			justify-content: space-between;
			align-items: center;
			padding: 14px 20px;
			padding-right: 15px;
			gap: 10px;
			font-weight: 500;
			font-size: 14px;
			line-height: 1.21429;
			color: #093359;
			border: 1px solid #093359;
			background: none;
			margin-top: 9px;
		}
	}
	&-more {
		li,
		li a {
			font-weight: 400;
			line-height: 1.65;
			color: #000;
		}
	}
	@media screen and (max-width: $sm) {

		&-categories {
			display: none;
		}
	}
}
