.practices {
	position: relative;
	@include container-with-tabs;

	&__footer {
		margin-top: 90px;
		display: flex;
		justify-content: space-between;
		align-items: center;
		gap: 70px;
		&-text {
			flex: 1;
		}
		&-btn {
			width: 235px;
		}
	}
	@media screen and (max-width: $md) {
		&__footer {
			padding-top: 16px;
			padding-bottom: 26px;
			&-text {
				display: none;
			}
		}
	}
	@media screen and (max-width: $sm) {
		&__footer {
			margin-top: 30px;
			gap: 20px;
			&-btn {
				width: 164px;
				height: 38px;
			}
		}
	}
}
