.media-wrapper {
	&__grid {
	}

	&__grid {
		display: grid;
		grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
		grid-gap: 70px 30px;
		@media screen and (max-width: $sm) {
			grid-template-columns: 1fr;
		}
	}

	&__card {
	}

	&__card-img {
		width: 100%;
		height: 266px;
		overflow: hidden;
		margin-bottom: 30px;
		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__card-info {
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin-bottom: 30px;
	}

	&__card-date {
		font-style: italic;
		line-height: 1.25;
		color: #4d565d;
	}
	&__card-cat {
		font-style: italic;
		line-height: 1.25;
		text-align: right;
		color: #000;
	}

	&__card-title {
		font-weight: 500;
		font-size: 18px;
		line-height: 1.65;
		text-decoration: underline;
		text-decoration-skip-ink: none;
		color: #000;
		margin-bottom: 12px;
	}

	&__card-tag {
		font-style: italic;
	}

	&__pagination {
		margin: 0 auto;
		display: flex;
		justify-content: center;
		align-items: center;
		margin-top: 90px;
		gap: 150px;
		@media screen and (max-width: $md) {
			justify-content: space-between;
			gap: 0;
		}
	}
	&__pagination-items {
		display: flex;
		gap: 20px;
		:not(:first-child)::before {
			content: "/";
			color: #4d565d;
			position: absolute;
			left: -12px;
			top: 50%;
			transform: translateY(-50%);
		}
	}
	&__pagination-links {
		display: flex;
		gap: 20px;
		:last-child::before {
			content: "";
			position: absolute;
			left: -10px;
			top: 50%;
			transform: translateY(-50%);
			display: block;
			width: 1px;
			height: 12px;
			background: #4d565d;
		}
	}
	&__pagination-link {
		position: relative;
		font-size: 14px;
		line-height: 1.65;
		text-align: center;
		color: #4d565d;
		&:hover {
			color: $base-color;
		}
	}
}
