.tabs {
	&__header {
		display: flex;
		justify-content: space-between;
		gap: 50px;
		align-items: center;
	}
	&__footer {
		padding: 16px 0 26px 0;
		&-btn {
			width: 164px;
			height: 38px;
		}
	}
	&__wrapper {
		display: flex;
		justify-content: space-between;
		flex: 1;
	}
	&__tab {
		cursor: pointer;
		// width: 390px;
		flex: 1;
		height: 60px;
		display: inline-flex;
		align-items: center;
		justify-content: center;
		font-weight: 500;
		line-height: 1.3;
		text-transform: uppercase;
		color: $text-gray;
		border-bottom: 2px solid $text-blue;
		border-top: 2px solid $text-blue;
		&--active {
			color: $text-blue;
		}
	}
	&__content {
		margin-top: 60px;
		height: 100%;
	}
	&__tab-content {
		height: 100%;
		display: none;

		&--active {
			display: flex;
		}
	}
	&__col {
		flex: 1 1 calc(100% / 3);
	}

	&__btns {
		display: none;
	}
	@media screen and (max-width: $md) {
		&__header {
			flex-direction: column;
			gap: 20px;
		}
		&__col {
			flex: none;
		}
		&__title {
			width: 100%;
			text-align: left;
		}
		&__wrapper {
			width: 100%;
		}
		&__tab {
			height: 44px;
			border-bottom: 1.5px solid #1f4569;
			border-top: 1.5px solid #1f4569;
			flex: 1;
		}
		&__content {
			margin-top: 30px;
		}
		&__tab-content {
			overflow: hidden;
			gap: 20px;
		}

		&__btns {
			padding: 16px 25px;
			padding-bottom: 26px;

			display: flex;
			justify-content: space-between;
			align-items: flex-start;
			background-color: #fff;
			gap: 15px;
			position: absolute;
			right: 0;
			bottom: 0;
		}
		&__btn {
			width: 90px;
			height: 57px;
		}
	}
	@media screen and (max-width: $sm) {
		&__col {
			flex: none;
		}

		&__btn {
			width: 60px;
			height: 38px;
		}
	}
}
